import { types } from '../types/types'

const initialState = {
  login: false,
  msgError: null,
}

export const uiReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.uiSetError:
      return {
        ...state,
        msgError: action.payload,
      }

    case types.uiRemoveError:
      return {
        ...state,
        msgError: null,
      }

    case types.uiStartLoading:
      return {
        ...state,
        login: true,
      }

    case types.uiFinishLoadingf:
      return {
        ...state,
        login: false,
      }

    default:
      return state
  }
}
